import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay, Navigation, Pagination } from 'swiper';
import 'swiper/css';
import { Link } from 'react-router-dom';

SwiperCore.use([Navigation, Pagination, Autoplay])



function Carousel(props) {
    const slides = [];
    //let currentDate = new Date();
    for (let i = 0; i < props["events"].length; i++) {
        let description = props["events"][i]["description"];
        let id = props["events"][i]["_id"]
        description = description.length < 300 ? description : description.slice(0, 300) + "...";
        slides.push(<SwiperSlide key={`slide-${i}`}>
            <div className='w-full h-full rounded-lg'>
                <img src={process.env.REACT_APP_API_URL + '/' + props["events"][i]["main_img"]} alt="img" className="w-full h-full object-cover rounded-lg" />
                {/* OVERLAY OVER IMAGE */}
                <div className='w-full h-full bg-black bg-opacity-40 z-10 relative top-[-100%] rounded-lg flex'>
                    <div className='ml-28 m-auto'>
                        <h1 className='text-2xl font-bold text-white underline underline-offset-8 decoration-2 decoration-rose-600'>{props["events"][i]["name"]}</h1>
                        <p className='text-white w-2/4 mt-4'>{description}</p>
                        {/* BUTTON  */}
                        <div className='flex flex-row mt-5'>
                            <Link to={"/events/" + id} className='p-7 pt-[0.25rem] pb-1 bg-rose-600 rounded-md text-white'>Register</Link>
                            <Link to={"/events/" + id} className=' ml-5 p-7 pt-[0.25rem] pb-1 bg-purple-600  rounded-md text-white'>View More</Link>
                        </div>
                    </div>
                </div>
            </div>
        </SwiperSlide>)
    }

    return (
        <>
            <div>
                <div className='m-auto flex container object-cover overflow-hidden '>
                    <Swiper
                        id="main"
                        className='w-full h-[80vh]'
                        tag="section"
                        wrapperTag='ul'
                        slidesPerView={1}
                        spaceBetween={0}
                        navigation
                        loop
                        autoplay={true}
                        pagination={{
                            clickable: true,
                        }}
                        modules={[Pagination, Navigation]}>
                        {
                            slides
                        }
                    </Swiper>
                </div>
            </div>
        </>
    )
}

export default Carousel;