import React from 'react'
import { useState } from 'react';
import { Link } from 'react-router-dom'
import SectionTitle from '../Components/SectionTitle'




function LeftTimeCard({ events }) {

  let dates = events.event_date.from;
  dates = new Date(dates);
  dates = `${dates.getDate()}-${dates.getMonth() + 1}-${dates.getFullYear()}`;

  return (
    <div className='flex flex-row w-full'>
      <div className='flex-[15] flex text-right'>
        <div className=' flex flex-row'>
          <div className='flex-[1]'></div>
          <div className='flex-[2] h-max my-auto'>
            <div>
              <h1 className='text-xl text-white font-bold'>{events.name}</h1>

              <p className='mt-2 mb-4 max-w-xl text-white/70'>{events.description.length < 100 ? events.description : events.description.slice(0, 100) + '...'}</p>
              <Link to="/" className='p-7 pt-[0.25rem] pb-1 bg-purple-600  rounded-md text-white'>Gallery</Link>
            </div>
          </div>
          <div className='ml-5 flex-[1] scale-[1.09]'>
            <img className='h-full object-cover rounded-lg' alt={"event"} src={process.env.REACT_APP_API_URL + '/' + events.main_img} />
          </div>
        </div>
      </div>
      <div className='flex-[1] flex flex-col items-center ml-2'>
        <div className='flex-[2] w-0.5 bg-white'></div>
        <div className='flex-[1] w-8 items-center justify-center rounded-full flex border-solid border-2 border-white'>
          <div className='w-5 h-2/3 bg-purple-400 rounded-full'></div>
        </div>
        <div className='flex-[2] w-0.5 bg-white'></div>
      </div>
      <div className='flex-[15] flex flex-col justify-center'>
        <h2 className='text-white text-xl ml-4'>{dates}</h2>
      </div>
    </div>
  )
}

function RightTimeCard({ events }) {
  let dates = events.event_date.from;
  dates = new Date(dates);
  let eventDate = `${dates.getDate()}-${dates.getMonth() + 1}-${dates.getFullYear()}`;

  return (
    <div className='flex w-full flex-row-reverse'>
      <div className='flex-[15] flex text-left'>
        <div className=' flex flex-row-reverse '>
          <div className='flex-[1]'></div>
          <div className='flex-[2] h-max my-auto ml-5'>
            <div>
              <h1 className='text-xl text-white font-bold'>{events.name}</h1>
              <p className='mt-2 mb-4 max-w-xl text-white/70'>{events.description.length < 100 ? events.description : events.description.slice(0, 100) + '...'}</p>
              <Link to="/" className='p-7 pt-[0.25rem] pb-1 bg-purple-600  rounded-md text-white'>Gallery</Link>
            </div>
          </div>
          <div className='ml-5 flex-[1] scale-[1.09]'>
            <img className='h-full object-cover rounded-lg' alt={"event"} src={process.env.REACT_APP_API_URL + '/' + events.main_img} />
          </div>
        </div>
      </div>
      <div className='flex-[1] flex flex-col items-center ml-2'>
        <div className='flex-[2] w-0.5 bg-white'></div>
        <div className='flex-[1] w-8 items-center justify-center rounded-full flex border-solid border-2 border-white'>
          <div className='w-5 h-2/3 bg-purple-400 rounded-full'></div>
        </div>
        <div className='flex-[2] w-0.5 bg-white'></div>
      </div>
      <div className='flex-[15] flex flex-col justify-center text-right'>
        <h2 className='text-white text-xl ml-4'>{eventDate}</h2>
      </div>
    </div>
  )
}

function Timeline({ events }) {
  let leftCardSide = true;
  let eventTimeline = [];
  let [eventLenght, setEventLenght] = useState(events.length > 3 ? 3 : events.length);
  let [allVisited, setAllVisited] = useState(false);

  for (let i = 0; i < eventLenght; i++) {
    if (leftCardSide) {
      eventTimeline.push(<LeftTimeCard key={i} events={events[i]} />);
      leftCardSide = false;
    } else {
      leftCardSide = true;
      eventTimeline.push(<RightTimeCard key={i} events={events[i]} />);
    }
  }
  let [timeline, setTimeline] = useState(eventTimeline);


  async function UpdateTimeline() {
    if (eventLenght >= events.length) {
      setAllVisited(true);
    } else {
      let prevEventLenght = eventLenght;

      eventLenght += eventLenght + 3 > events.length ? events.length - eventLenght : 3;
      setEventLenght(eventLenght);

      if (eventLenght === events.length)
        setAllVisited(true);

      let eventTimeline = timeline.slice();

      for (let i = prevEventLenght + 1; i <= eventLenght; i++) {
        if (eventLenght === events.length) {
          setAllVisited(true);
          break;
        }
        if (leftCardSide) {
          eventTimeline.push(<LeftTimeCard key={i} events={events[i]} />);
          leftCardSide = false;
        } else {
          leftCardSide = true;
          eventTimeline.push(<RightTimeCard key={i} events={events[i]} />);
        }
      }

      setTimeline(eventTimeline);
    }
  }


  return (
    <div className='container m-auto mt-10'>
      <SectionTitle title='Timeline' />
      <div className='flex w-full flex-row h-20'>
        <div className='flex-[15] flex text-left'>
        </div>
        <div className='flex-[1] flex flex-col items-center ml-2'>
          <div className='flex-[1] w-8 items-center justify-center rounded-full flex border-solid border-2 border-white'>
            <div className='w-5 h-2/3 bg-rose-500 rounded-full'></div>
          </div>
          <div className='flex-[2] w-0.5 bg-white'></div>
        </div>
        <div className='flex-[15] flex flex-col justify-center text-left'>
          <h2 className='text-white text-xl ml-4'>Current Day</h2>
        </div>
      </div>
      {timeline}
      {allVisited ? <>
        <div className='flex flex-col items-center mt-3 ml-2'>
          <h2 className='text-xl text-white'>End of Timeline</h2>
        </div ></> : <>
        <div className='h-11 flex flex-col items-center'>
          <div className='ml-2 h-full w-0.5 bg-white'></div>
          <div className='ml-2 h-full w-0.5'></div>
          <div className='ml-2 h-full w-0.5 bg-white'></div>
          <div className='ml-2 h-full w-0.5'></div>
          <div className='ml-2 h-full w-0.5 bg-white'></div>
          <div className='ml-2 h-full w-0.5'></div>
          <div className='ml-2 h-full w-0.5 bg-white'></div>
          <div className='ml-2 h-full w-0.5'></div>
          <div className='ml-2 h-full w-0.5 bg-white'></div>
          <div className='ml-2 h-full w-0.5'></div>
        </div>
        <div className='flex flex-col items-center mt-3 ml-2'>
          <h2 className='text-xl text-white'>Explore More</h2>
          <button onClick={() => UpdateTimeline()} className="bg-purple-600 rounded-full w-10 h-10 flex flex-row justify-center items-center"><svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="white" className="w-5 h-5">
            <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 8.25l-7.5 7.5-7.5-7.5" />
          </svg>
          </button>
        </div >
      </>
      }
    </div >
  )
}

export default Timeline