import React from 'react'
import { LazyLoadImage } from 'react-lazy-load-image-component'
import { Link } from 'react-router-dom'
import calender from "../../assets/img/calender.svg"
import time from "../../assets/img/time.svg"

function EventCard({ dates, heading, imgsrc, eventid, eventtime }) {
  return (
    <div className="container rounded-xl ease-in-out bg-gradient-to-r p-1 from-plupler to-black hover:from-black hover:to-plupler">
      <div className='w-full p-2'>
        <LazyLoadImage className='object-cover rounded-md min-w-full max-w-full h-[200px]' alt="Event" src={imgsrc} loading='lazy' />
      </div>
      <h2 className='text-white text-center text-2xl'>{heading}</h2>
      <div className='w-full flex flex-row'>
        <div className='p-3 flex flex-col flex-[12] items-center'>
          <LazyLoadImage src={calender} alt="time" className='w-[30px] h-[30px]' />
          <p className='text-white text-center'>{dates}</p>
        </div>
        <div className='min-h-full flex-1 flex flex-col justify-center'><div className="w-[2px] h-9 bg-white rounded-xl"></div></div>
        <div className='p-3 flex flex-col flex-[12] items-center'>
          <LazyLoadImage src={time} alt="time" className='w-[30px] h-[30px]' />
          <p className='text-white text-center'>{eventtime}</p>
        </div>
      </div>
      <div className='flex flex-row justify-evenly mb-5'>
        <Link to={"/event/register/" + eventid} className="ml-5 p-7 pt-[0.25rem] pb-1 bg-purple-600 bg-opacity-50  rounded-md text-white">Register</Link>
        <Link to={"/events/" + eventid} className="ml-5 p-7 pt-[0.25rem] pb-1 bg-purple-600  rounded-md text-white">View More</Link>
      </div>
    </div>
  )
}

export default EventCard