import React from 'react'
import { useParams } from 'react-router-dom'
import EventPageTop from '../Sections/EventPageTop/EventPageTop'
import EventPageBottom from '../Sections/EventPageBottom/EventPageBottom'
import { useState, useEffect } from 'react'
import { get } from 'jquery'
import { getEvent } from '../api/apis'


function EventsPage() {
  const [event, setEvent] = useState(null);
  const { id } = useParams();

  useEffect(() => {
    const fetchData = async () => {
      const res = await getEvent(id);
      const event = (await res);
      setEvent(event["event"]);
      console.log(event);
    }

    fetchData();
  }, [])

  return (
    <>
      {
        event !== null && event !== undefined ? <div>
          <div>
            <EventPageTop event={event} />
          </div>
          <div>
            <EventPageBottom event={event} />
          </div>
        </div> : <div>Loading...</div>

      }
    </>
  )
}

export default EventsPage