import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';

function Gallery({ gallery }) {
    const [viewGallery, setGallery] = useState([]);

    async function viewMore() {
        let temp_img_gallery = viewGallery.slice();
        const currentLength = viewGallery.length;
        console.log(viewGallery);
        if (currentLength === gallery.length) {
            console.log(currentLength)
            return;
        }
        const nextLenght = currentLength + 6 <= gallery.length ? currentLength + 6 : currentLength + gallery.length % 6;
        for (let i = currentLength; i < nextLenght; i++) {
            temp_img_gallery.push(
                <div key={i} className='min-h-[300px] max-h-[300px] min-w-[400px] max-w-[400px] '>
                    <a href={process.env.REACT_APP_API_URL + '/' + gallery[i]} target={'_blank'} rel={'noreferrer'}>
                        <LazyLoadImage className="object-cover h-full w-full hover:scale-105 rounded-md" alt="events" src={process.env.REACT_APP_API_URL + '/' + gallery[i]} />
                    </a>
                </div>
            )
        }

        setGallery(temp_img_gallery)
    }


    useEffect(() => {
        viewMore();
        // eslint-disable-next-line
    }, []);

    return (<>
        <div className='container flex flex-row flex-wrap gap-5 justify-center my-7'>
            {viewGallery.length === 0 ? <div>No Gallery Image</div> : <>{viewGallery.map((value, index) => { return value; })}
                <div className='container flex flex-col items-center'>
                    {viewGallery.length === gallery.length ? <></> : <>
                        <h2 className='text-xl text-white'>View More</h2>
                        <button onClick={() => { viewMore() }} className="bg-purple-600 rounded-full w-10 h-10 flex flex-row justify-center items-center"><svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="white" className="w-5 h-5">
                            <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 8.25l-7.5 7.5-7.5-7.5" />
                        </svg>
                        </button>
                    </>
                    }
                </div>

            </>}
        </div>
    </>);
}

export default Gallery