import React from 'react'
import { useEffect, useState } from 'react';
import { getAllEventPage } from '../api/apis';
import EventCard from '../Components/Card/EventCard';
function Events() {

  const [data, setData] = useState(null);

  //for fetching data form server
  useEffect(() => {
    async function fetchData() {
      const response = await getAllEventPage();
      if (response.hasOwnProperty('error')) {
        console.error(response);
      }
      console.log(response);
      setData(response["events"]);
    }
    fetchData()
  }, [])



  return (
    <div className='container m-auto mt-5'>
      <div className='flex m-auto flex-col justify-center md:flex-row border-b-2 pb-8 border-white/50'>
        <div className='flex flex-wrap m-auto md:m-0 '>
          <h1 className='text-5xl text-center self-center text-skin-title font-extrabold title-font'>ALL EVENTS</h1>
        </div>
        <div className='flex p-1 ml-auto m-auto md:mr-0'>
          <div className='bg-transparent flex border border-3 focus:ring-purple-300 focus:outline-none focus:ring  focus:ring-opacity-40 rounded-xl focus:border-3 mr-6' >
            <svg className='m-auto ml-2' width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M24.6849 23.2181L17.512 16.0343C18.9261 14.337 19.7766 12.168 19.7766 9.799C19.7766 4.39442 15.3448 0 9.89343 0C4.44205 0 0 4.39955 0 9.80412C0 15.2087 4.43181 19.6031 9.88318 19.6031C12.199 19.6031 14.3304 18.8083 16.0211 17.4803L23.2196 24.6846C23.6397 25.1051 24.2648 25.1051 24.6849 24.6846C25.105 24.2642 25.105 23.6386 24.6849 23.2181ZM2.10063 9.80412C2.10063 5.5584 5.59484 2.10748 9.88318 2.10748C14.1715 2.10748 17.6657 5.5584 17.6657 9.80412C17.6657 14.0498 14.1715 17.5008 9.88318 17.5008C5.59484 17.5008 2.10063 14.0447 2.10063 9.80412Z" fill="white" /></svg>
            <input
              type="Type here ..."
              className="block w-full px-4 py-2 focus:outline-0 text-white bg-transparent   "
              placeholder="Type here ..."
              icon="search"
            />
          </div>
          <div className="relative">
            <select className="block appearance-none  w-full bg-skin-fill-200 border border-3 text-skin-base py-3 px-4 pr-3 rounded-lg leading-tight focus:outline-none focus:bg-skin-fill-200 mr-4 ">
              <option>Sort By</option>
              <option>With options</option>
            </select>
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-skin-base">
              <svg width="13" height="8" viewBox="0 0 13 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0 0.760742L6.50002 7.2396L13 0.760742H0Z" fill="white" /></svg>
            </div>
          </div>
        </div>
      </div>
      {/* Event card coming in grid view */}
      <div className='container cursor-grab grid mt-8 m-3 grid-cols-1  md:grid-cols-3 xl:grid-cols-4 justify-items-center sm:grid-cols-2  max-w-full  place-content-center gap-10  overflow-hidden'>
        {data === null || data === undefined ? <div className='text-red-400'>Loading...</div> :
          data.map((event, index) => {
            let start_date_1 = new Date(event.event_date.from);
            let end_date_1 = new Date(event.event_date.from);
            const start_date = start_date_1.getUTCDate().toString() + '-' + start_date_1.getUTCMonth().toString() + '-' + start_date_1.getUTCFullYear().toString();
            const end_date = end_date_1.getUTCDate().toString() + '-' + end_date_1.getUTCMonth().toString() + '-' + end_date_1.getUTCFullYear().toString();

            const start_time = start_date_1.getUTCHours().toString() + ':' + start_date_1.getUTCMinutes().toString();
            const end_time = end_date_1.getUTCHours().toString() + ':' + end_date_1.getUTCMinutes().toString();
            return (
              <EventCard
                dates={start_date + ' - ' + end_date}
                eventid={event.id}
                eventtime={start_time + ' - ' + end_time}
                heading={event.name}
                imgsrc={process.env.REACT_APP_API_URL + '/' + event.main_img}
                key={index} />
            );
          })}
      </div>
    </div>
  )
}

export default Events