import React from 'react'
import SectionTitle from '../Components/SectionTitle'
import UpcomingCard from '../Components/Card/UpcomingCard';
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
function Upcoming(props) {

  let slides = [];

  for (let i = 0; i < props["events"].length; i++) {
    console.log(props["events"]["_id"])
    slides.push(
      <SwiperSlide key={i}>
        <UpcomingCard
          eventid={props["events"][i]["_id"]}
          dates={props["events"][i]["event_date"]["from"]}
          description={`${props['events'][i]["description"]}`}
          heading={`${props["events"][i]["name"]}`}
          imgsrc={`${process.env.REACT_APP_API_URL}/${props["events"][i]["main_img"]}`}
        />
      </SwiperSlide>
    )
  }

  return (
    <div className='container m-auto mt-10'>
      <SectionTitle title='Upcoming Events' />
      <div className='container m-auto flex flex-row w-[80vw] mb-49 '>
        {props["events"].length === 0 ? <div className=' w-full text-center'><h2 className='text-2xl font-bold text-rose-500 font-mono'>Currently no Upcoming Events</h2></div> : <Swiper
          slidesPerView={props["events"].length <= 2 ? props["events"].length : 3}
          grabCursor={true}
          spaceBetween={30}
          className="mySwiper"
          mousewheel={true}
        //slidesPerView={"auto"}
        >
          {slides}
        </Swiper>}
      </div>

    </div>
  )
}

export default Upcoming