import React from 'react'
import { Link } from 'react-router-dom'
function EventPageTop({ event }) {
  let event_date = new Date(event.event_date.from);
  return (
    <section className="text-gray-400   body-font">
      <div className="container px-5 py-24 mx-auto flex flex-wrap">
        <div className='md:w-3/5'>
          <h1 className="sm:text-3xl text-2xl text-white font-medium title-font mb-2 ">{event.name}</h1>
          <h2 className='sm:text-3xl text-2xl text-skin-pink font-medium title-font mb-2'> [{event_date.getDate() + ' ' + event_date.toLocaleDateString('default', { month: 'short' }) + ' ' + event_date.getFullYear()}]  </h2>
          <p className='sm:text-md text-md md:w-4/5 text-white mb-2 mr-auto'>
            {event.description}
          </p>
          {
            new Date(event.registration_datetime.to) <= Date() ?
              <button className='inline-flex text-white mt-5 dark:text-red-400 bg-skin-pink border-0 py-1 px-4 focus:outline-none hover:bg-indigo-600 rounded'>
                <Link to={'/events/fd'}> Button</Link>
              </button>
              : <></>
          }
        </div>

        <div className="md:w-2/5 md:pl-6">
          <img className="rounded-2xl min-h-[300px] max-h-[360px]" src={process.env.REACT_APP_API_URL + '/' + event.main_img} alt="img" />
        </div>
      </div>
    </section>
  )
}

export default EventPageTop