import React from 'react'
import { useState } from 'react'
import ButtonTab from '../../Components/ButtonTab'
import EventMore from '../EventMore';
import Gallery from '../Gallery';

function EventPageBottom({ event }) {
  const [toggle, setToggle] = useState('gallery');

  /** It change the Tab of gallery/more
   * 
   * @param {string} state 
   */
  function setToggleState(state) {
    setToggle(state);
  }

  /** Function for getting the tab of event page
   *  
   * @param {string} tabname 
   * @returns {React.FunctionComponent} function componet for tab if not availabe return React.Fragment
   */
  function getTab(tabname) {

    if (tabname === 'gallery') {
      return <Gallery gallery={event.gallery} />
    } else if (tabname === 'more') {
      return <EventMore section={event.section} />
    }
    return <></>
  }

  return (
    <div className='container rounded-2xl m-auto bg-transparent'>
      <ButtonTab func={(i) => setToggleState(i)} active={toggle} />
      {getTab(toggle)}
    </div>
  )
}
export default EventPageBottom