import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import pulogo from '../assets/img/pulogo.svg'

function Navbar() {
  const headerClasses = `text-skin-base body-font shadow-lg  `;
  const headerClasses2 = `w-full m-auto  body-font bg-white shadow-lg sticky top-0 z-[1000] shadow-white `;

  const [header, setHeader] = useState(`${headerClasses}`)
  const listionScrollEvent = (event) => {
    if (window.scrollY < 73) {
      return setHeader(`${headerClasses}`)
    } else if (window.scrollY > 73) {
      return setHeader(`${headerClasses2}`)
    }
  }

  useEffect(() => {
    window.addEventListener('scroll', listionScrollEvent);
    return () =>
      window.removeEventListener('scroll', listionScrollEvent);
  }, []);
  return (
    <header className={header}>
      <div className="container mx-auto flex flex-wrap p-3 flex-col md:flex-row items-center">
        <Link to="/" className="flex  items-center mb-4 md:mb-0">
          <img src={pulogo} alt="logo" className="w-30 h-10 mr-2" />
        </Link>
        <nav className="md:ml-auto flex flex-wrap items-center text-base justify-center">
          <Link to="/" className="mr-5 hover:text-gray-200/80">Home</Link>
          <Link to="/events" className="mr-5 hover:text-gray-200/80">Events</Link>
          <Link to="/about" className="mr-5 hover:text-gray-200/80">About</Link>
          <Link to="/contactus" className="mr-5 hover:text-gray-200/80 ">Contact Us</Link>
        </nav>
        <Link to="/login" className="inline-flex items-center bg-skin-fill-200 border-0 py-1 px-3 focus:outline-none  rounded  text-skin-base mt-4 md:mt-0">Login</Link>
      </div>
    </header>
  )
}

export default Navbar