import React, { useState, useEffect } from 'react'
import Carousel from '../Components/Carousel'
import OurSponser from '../Sections/OurSponser'
import Upcoming from '../Sections/Upcoming'
import Timeline from '../Sections/Timeline'
import Subscribe from '../Components/Subscribe'
import { getHomePage } from '../api/apis'


function Home() {
  const [data, setData] = useState(null);

  //for fetching data form server
  useEffect(() => {
    async function fetchData() {
      const response = await getHomePage();
      setData(response);
    }
    fetchData()
  }, [])

  return (
    data != null ?
      <div >
        <Carousel events={data.carousel_events} />
        <Upcoming events={data.upcoming_events} />
        <Timeline events={data.timeline_events} />
        <OurSponser />
        <Subscribe />
      </div> : <div>Loading...</div>
  )
}

export default Home