import React from 'react'
import { Link } from 'react-router-dom'

function Error404() {
  return (
   
    <div className='container rounded-2xl m-auto bg-white bg-erro-404 bg-no-repeat bg-center bg-contain'>
      <div className=' items-center justify-center flex flex-col ml-[-80px] min-h-[70vh] '>
        <img className='mb-8 w-60' src="http://genesisexpo.wgl-demo.net/wp-content/themes/genesisexpo/img/404.png" alt="" />
         <h1 className=' text-4xl mb-2'>  Sorry We Can't Find That Page!</h1>
         <h2 className='mb-4 text-xl max-w-md text-center'> The page you are looking for was moved, removed, renamed or never existed.</h2>
         <Link to="/" className='inline-flex items-center bg-skin-fill-200 py-2 px-3 focus:outline-none  rounded-2xl text-base  text-white'>Take Me Home</Link>
      </div>
    </div>
  )
}

export default Error404