import { Link } from 'react-router-dom'
import calender from "../../assets/img/calender.svg"
function UpcomingCard({ dates, heading, description, imgsrc, eventid }) {
  dates = new Date(dates);
  let date = `${dates.getDate()}-${dates.getMonth() + 1}-${dates.getFullYear()}`;
  return (
    <div key={eventid} className="w-full bg-gray-800 bg-opacity-40 rounded-2xl flex flex-col flex-1">
      <img className="rounded rounded-t-2xl max-h-64 object-cover" src={imgsrc} alt="content" />
      <div className='p-6 pt-2'>
        <h3 className="title-font text-white text-xl">{heading}</h3>
        <div className='flex mb-2 mt-2'>
          <img className='mr-1' src={calender} width="20px" height="20px" alt='icon' />
          <h2 className="text-skin-purple text-md">{date}</h2>
        </div>
        <p className="text-sm mb-4 text-white/70">{description.length < 60 ? description : (description.slice(0, 60) + '...')}.</p>
        <Link to={`/events/${eventid}`} className="bg-purple-600 text-md px-7 p-2 rounded-md text-white title-font">Read More</Link>
      </div>
    </div>
  )
}

export default UpcomingCard
