import React from 'react'

function ButtonTab({ func, active }) {
  function changeTab(tab) {
    func(tab);
  }

  let notactive = "inline-flex items-center px-16 py-4 bg-white border-0 focus:outline-none max-w-md  p-auto  rounded-xl rounded-tr-none ";
  let active_state = 'inline-flex items-center px-16  py-4 bg-skin-purple border-0  focus:outline-none  rounded-xl rounded-bl-none ';


  return (
    <div className='m-auto max-w-max bg-gradient-to-b from-white to-purple rounded-xl'>
      <button
        className={active == 'gallery' ? active_state : notactive}
        onClick={() => changeTab('gallery')}
      >
        Gallery
      </button>
      <button
        className={active == 'more' ? active_state : notactive}
        onClick={() => changeTab('more')}
      >
        View More
      </button>
    </div>
  )
}

export default ButtonTab