


export async function getHomePage() {
    const data = await fetch(process.env.REACT_APP_API_URL + '/get/home').then(res => {
        if (res.ok)
            return res.json();
        else
            return { "error": true, "res": res.json() };
    });

    return data;
}

export async function getAllEventPage() {
    const data = await fetch(process.env.REACT_APP_API_URL + '/get/events/all').then(res => {
        if (res.ok || res.status === 304)
            return res.json();
        else
            return { "error": true, "res": res.json() };
    });
    return data;
}

export async function getEvent(id) {
    const data = await fetch(process.env.REACT_APP_API_URL + '/get/event/id/' + id).then(res => {
        if (res.ok || res.status === 304)
            return res.json();
        else
            return { "error": true, "res": res.json() };
    });
    return data;
}